import { _decorator, Component, Node, Collider2D, Contact2DType, math, Vec3, tween, IPhysics2DContact } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Boss')
export class Boss extends Component {
    @property
    speed: number = 100;
    public randomPosition;
    start() {
        let collider = this.getComponent(Collider2D);
        if (collider) {
            collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
        }

        this.moveRandom();
    }

    update(deltaTime: number) {
        
    }
    moveRandom() {
        const randomX = math.random() * (0 - 1000) + 500;
        const randomY = math.random() * (200 - 600) + 200;
        this.randomPosition = new Vec3(randomX, randomY);

        const distance = Vec3.distance(this.node.position, this.randomPosition);
        const duration = distance / this.speed;

        tween(this.node)
            .to(duration, { position: this.randomPosition })
            .call(() => {
                this.moveRandom();
            })
            .start();
    }
    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null){
        let other = otherCollider.node;
        let self = selfCollider.node;
    }
    onEndContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null){

    }
}


